/* Default table */

table {
    border-collapse: separate;
    border-spacing: 0;
    max-width: 100%;
    width: 100%;
}

th {
    text-align: left;
    font-weight: $font-weight-base;
}

th,
td {
    padding: 5px;
    line-height: inherit;
}

th { vertical-align: bottom }
td { vertical-align: top }
